.has-fade{

  visibility: hidden;
}

@keyframes fade-in{
  from{
    visibility: hidden;
    opacity: 0;
  }
  1%{
         visibility: visible;
          opacity: 0;
  }
  to{
      visibility:visible;
      opacity: 1;
  }
}
.fade-in {
  animation: fade-in 100ms  ease-in-out forwards;// the 200  for the spead of the animatinos
}

@keyframes fade-out{
    from{
      visibility: visible;
      opacity: 1;
    }
    99%{
      visibility: visible;
      opacity: 1;
    }
    to{
      visibility: hidden;
      opacity: 0;
    }
}

.fade-out{
  animation: fade-out 100ms ease-in-out forwards;// the 200  for the spead of the animatinos
}

